<template>
  <el-table
    :data="items"
    border
    size="mini"
    style="width: auto">
    <el-table-column type="index" width="35" align="center" />
    <el-table-column 
      v-for="(c, i) in columnDefs"
      :key="i"
      :label="c.label"
      :prop="c.prop"
      :sortable="c.sortable">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'profile-roles',

  mounted() {
    this.$store.dispatch('getRoles')
      .then(roles => this.items = roles.filter(r => this.$store.state.roles.includes(r.id)))
  },

  data: () => ({
    items: [],
    columnDefs: [
      { label: 'Role name', prop: 'id', sortable: true },
      { label: 'Description', prop: 'description', sortable: true },
      { label: 'Systems', prop: 'system', sortable: true }
    ]
  })
}
</script>



