<template>
  <el-form 
    label-position="left" 
    label-width="130px" 
    size="small">
    <el-row :gutter="30">
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Username">
          <span>{{ current_user.username }}</span>
        </el-form-item>
        <el-form-item label="Full name">
          <span>{{ current_user.fullname }}</span>
        </el-form-item>
        <el-form-item label="Email">
          <span>{{ current_user.email }}</span>
        </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Department">
          <span>{{ current_user.department }}</span>
        </el-form-item>
        <el-form-item label="Sub department">
          <span>{{ current_user.department_lv2 }}</span>
        </el-form-item>
        <el-form-item label="Title">
          <span>{{ current_user.title }}</span>
        </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Joined date">
          <span :title="current_user.created_at">
            {{ current_user.created_at | moment("from", "now") }}
          </span>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'profile-general-information',

  computed: {
    ...mapGetters(['current_user'])
  }
}
</script>
<style lang="css" scoped>
>>>.el-form-item--small.el-form-item {
  margin-bottom: 0 !important;
}
</style>



