<template>
  <el-collapse v-model="activePanels">
    <el-collapse-item 
      v-for="(p, i) in panels"
      :key="i"
      :name="i">
      <template slot="title">
        <i :class="p.icon" />
        <span class="primary--text">&nbsp;{{ p.label }}</span>
      </template>
      <component :is="p.component" class="mx-3" />
    </el-collapse-item>
  </el-collapse>
</template>

<script>
import GeneralInformation from '@/components/Profile/General'
import Security from '@/components/Profile/Security'
import Role from '@/components/Profile/Role'

export default {
  name: 'app-profile',

  components: {
    GeneralInformation,
    Security,
    Role
  },

  data: () => ({
    activePanels: [0, 1, 2],
    panels: [
      { label: 'General Information', icon: 'el-icon-s-custom', component: 'GeneralInformation' },
      { label: 'Security', icon: 'el-icon-lock', component: 'Security' },
      { label: 'Roles', icon: 'icon icon-warning', component: 'Role' }
    ]
  })
}
</script>



